.btn {
    @apply flex items-center justify-center py-3 px-10 lg:px-20 font-bold;
    border: 1px solid #1f8a70;
    border-radius: 25px;
    letter-spacing: 0.5px;

    &__primary {
        @apply bg-secondary text-white;
    }
}

.grid__posts {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;

    @screen md {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
    }

    @screen lg {
        display: grid;
        height: 424px;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-template-areas:
            "a a b b"
            "a a c d";
    }



    .grid__posts__item1 {
        @screen lg{
            grid-area: a;
        }

        .title {
            @apply title--5 lg:title--4;
            text-transform: initial !important;
        }
    }
    .grid__posts__item2 {
        @screen lg{
            grid-area: b;
        }

        .title {
            @apply title--5 lg:title--5;
            text-transform: initial !important;
        }
    }
    .grid__posts__item3 {
        @screen lg {
            grid-area: c;
        }

        .title {
            @apply title--5 lg:title--5;
            text-transform: initial !important;
        }
    }
    .grid__posts__item4 {
        @screen lg {
            grid-area: d;
        }

        .title {
            @apply title--5 lg:title--5;
            text-transform: initial !important;
        }
    }
}

.tag {
    @apply bg-secondary text-white inline-flex items-center justify-center py-0 px-2 font-bold font-manrope;
}

.grid__posts__item {
    @apply backgroundSettings relative flex items-end pb-2 px-4 h-56 w-full lg:h-auto;

    &:before {
        @apply absolute w-full h-full left-0 top-0;
        content: "";
        background-color: rgba(0, 0, 0, 0.6);
    }

    .content {
        @apply relative flex;

        .title {
            @apply text-white mb-0 mt-1;
        }

        span {
            @apply title--button text-primary-lighter font-manrope;
            font-weight: bold !important;
            letter-spacing: 0.5px;
            text-transform: capitalize !important;
        }
    }
}

.title__section{
    @apply pb-2 relative justify-between;
    border-bottom: 2px solid theme('colors.text');

    &:before{
        @apply bg-secondary absolute;
        content:'';
        display: block;
        width: 50px;
        height:6px;
        bottom:-4px;
        
    }
    
}

.featured__post{

    @apply flex flex-wrap gap-4 mt-4 lg:mt-10;

   

    @screen lg {
        display: grid;
        gap:1rem;
        height: 561px;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(3, 1fr);
        grid-template-areas:
            "a a b"
            "a a c"
            "a a d";
    }

    .grid__posts__item1{        
        grid-area: a; 
    }
    .item__post2{
        grid-area: b; 
    }
    .item__post3{
        grid-area: c; 
    }
    .item__post4{
        grid-area: d; 
    }
}

.item__post{
    .image{
        @apply h-[127px] w-full;

        img{
            @apply h-full w-full object-cover;
        }
    }
}

.item__latest{
    img{
        @apply md:h-full object-cover;
    }

    .title{
        @apply mb-2;
    }

    span {
        @apply title--button text-secondary font-manrope;
        font-weight: bold !important;
        letter-spacing: 0.5px;
        text-transform: capitalize !important;
    }

}
